var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno'),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5 mb-5"},[_vm._m(0),_c('p',{staticClass:"mb-5"},[_vm._v("Veamos una visión general de este importante tema a través del siguiente video.")]),_vm._m(1),_vm._m(2),_vm._m(3),_c('div',{staticClass:"fullwidth-bg fullwidth-bg--1"}),_vm._m(4),_c('separador'),_vm._m(5),_vm._m(6),_vm._m(7),_c('div',{staticClass:"row justify-center"},[_c('div',{staticClass:"col-12 col-xl-10",attrs:{"data-aos":"zoom-in-down"}},[_c('AcordionA',{staticClass:"mb-5",attrs:{"tipo":"a","clase-tarjeta":"tarjeta tarjeta--azul"}},[_c('div',{staticClass:"row",attrs:{"titulo":"Peligros físicos"}},[_c('div',{staticClass:"col-md-12 px-5"},[_c('p',[_vm._v("Es una sensación auditiva generalmente desagradable. Cuando oímos un ruido, primero lo escuchamos a través del oído externo, luego la onda es recibida por el oído medio, que es donde se encuentra el tímpano. Posteriormente, la señal pasa por una cadena de huesecillos y es recibida por el cerebro a través de las células ciliadas. Cuando el ruido es muy fuerte, las células ciliadas se activan y se corre el riesgo de perder la audición. Debemos protegernos con todas las medidas de seguridad posibles, los tipos de ruido pueden ser de impacto, intermitentes y continuos. Las vibraciones pueden ser segmentarias o de todo el cuerpo dependiendo de la herramienta utilizada, pueden afectar a la columna vertebral, al sistema digestivo, e incluso provocar dolores de cabeza. Los reflejos, las sombras, la fatiga y el deslumbramiento son factores producidos por la iluminación, que pueden ser visibles y causar lesiones por deficiencia o exceso. Estos elementos pueden provocar un accidente, por lo que es necesario controlar el tipo de lámparas y respetar los niveles de luz adecuados.")]),_c('p',{staticClass:"mb-0"},[_vm._v("La temperatura y la humedad del ambiente si son excesivamente altas o bajas pueden producir efectos adversos en las personas. Los valores ideales en el trabajo son 21 °C y 50% de humedad. Las radiaciones ionizantes son ondas electromagnéticas que alteran el estado físico sin ser percibidas en el ambiente, se conocen como rayos X, gamma, beta y alfa. Los efectos son graves a largo plazo, por eso es necesario limitar las ondas y tener un control médico. Y las radiaciones no ionizantes, como el láser, los rayos UV, los infrarrojos, la radioterapia o las microondas, también requieren un control. La presión atmosférica, que puede ser ajustada o normal.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Riesgos químicos"}},[_c('div',{staticClass:"col-md-12 px-5"},[_c('p',[_vm._v("Se producen por procesos químicos y por el medio ambiente. Ejemplos claros son los polvos (orgánicos e inorgánicos), las fibras, los líquidos (nieblas o aerosoles), los gases o vapores, los humos metálicos o no metálicos y las partículas. Enfermedades como las alergias, la asfixia o los virus se producen por inhalación, absorción o ingestión. Debemos protegernos con mascarillas, guantes y delimitar la zona de trabajo.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Riesgos biológicos"}},[_c('div',{staticClass:"col-md-12 px-5"},[_c('p',[_vm._v("Las enfermedades causadas por virus, bacterias, hongos, parásitos, picaduras, mordeduras, raquitismo y diversos fluidos corporales se deben al contacto con todo tipo de seres vivos o plantas, a través de la sangre, la orina, las heces, el pelo y la leche. Para evitarlos, se recomienda llevar un control de las vacunas y, sobre todo, protegerse con el equipo adecuado.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Riesgos ergonómicos"}},[_c('div',{staticClass:"col-md-12 px-5"},[_c('p',[_vm._v("La ergonomía es la ciencia que busca la adaptación integral al puesto de trabajo y al hombre. Los principales factores de riesgo ergonómico son: las posturas inadecuadas, que pueden ser prolongadas, mantenidas, forzadas o antigravitatorias; el levantamiento de pesos, los movimientos repetitivos. Pueden provocar daños físicos y molestias.")]),_c('p',{staticClass:"mb-0"},[_vm._v("Este tipo de riesgo ofrece cifras relativamente altas, ocupando el 60% de las enfermedades en los puestos de trabajo y el 25% se deben a la manipulación de golpes. Al levantar pesos, la espalda debe estar completamente recta y las rodillas dobladas. Si se trata de un trabajo físico, hay que estirar los músculos y las articulaciones antes de empezar para evitar futuras lesiones. Hay que utilizar métodos seguros en todo momento.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Riesgos psicosociales"}},[_c('div',{staticClass:"col-md-12 px-5"},[_c('p',[_vm._v("Este riesgo depende de la gestión organizativa, de las características del trabajo y de las características del grupo social, es realmente importante identificar, evaluar e intervenir en todos los aspectos socio-demográficos del empleado: recursos humanos: estilo de dirección, pagos, contratación, aspectos extra-laborales: amigos, tiempo libre, familia, individuales: personalidad, absentismo, edad, antecedentes, etc, algunas manifestaciones pueden ser: estrés, fatiga, monotonía, para prevenirlas es recomendable un compromiso por parte de la dirección para la priorización de los hallazgos. La estabilidad y un buen ambiente nos ayudará a disminuir estos riesgos.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Condiciones de seguridad"}},[_c('div',{staticClass:"col-md-12 px-5"},[_c('p',[_vm._v("La clasificación para este tipo de riesgo con mecánica como elementos o parte de máquinas, herramientas, equipos, piezas y materiales proyectados; eléctrica de alta o baja presión o estática; locativa como superficies de trabajo, condiciones de orden y limpieza y caída de objetos. Estos tipos de riesgos se reflejan en trabajos en altura, superficies inseguras, mal uso de herramientas, equipos defectuosos. Debemos asegurarnos siempre de revisar la maquinaria en la que trabajamos para evitar posibles incidentes.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Riesgos ambientales"}},[_c('div',{staticClass:"col-md-12 px-5"},[_c('p',[_vm._v("Son los únicos factores que no podemos controlar. Se manifiestan en la naturaleza lluvias, tormentas, inundaciones, desprendimientos, precipitaciones. Debemos ser previsores y prudentes.")])])])])],1)]),_c('div',{staticClass:"fullwidth-bg fullwidth-bg--3"}),_c('separador'),_vm._m(8),_vm._m(9),_vm._m(10),_vm._m(11),_vm._m(12),_vm._m(13),_c('div',{staticClass:"p-3 p-lg-4 bg-color-terciario-t1 z-2",attrs:{"data-aos":"fade-down"}},[_c('div',{staticClass:"row align-items-center"},[_vm._m(14),_c('div',{staticClass:"col"},[_c('div',{staticClass:"row justify-content-between align-items-center"},[_vm._m(15),_c('div',{staticClass:"col-sm-auto p-lg-4"},[_c('a',{staticClass:"boton bgr_3 me-3 text-white",attrs:{"href":_vm.obtenerLink('downloads/FORMATO_MATRIZ_DE_IDENTIFICACIÓN_DE_PELIGROS.xlsx'),"target":"_blank"}},[_c('span',[_vm._v("Enlace")]),_c('i',{staticClass:"fas fa-link"})])])])])])])],1)],1)
